@import url('https://fonts.googleapis.com/css2?family=Kode+Mono&display=swap'); /* Import Google Font */

.contact-container {
  position: relative;
  z-index: 1;
  padding: 20px;
  background: rgba(255, 255, 255, 0); /* Background with transparency */
  color: white;
  width: auto; /* Adjust the width as needed */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto; /* Center align the container */
  font-family: 'Kode Mono', monospace; /* Apply Google Font */
}

#contact {
  font-size: 1.2rem;
  line-height: 1.6;
  height: 100vh;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 10px; /* Add space between the contact items */
  align-items: center; /* Center align contact info */
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 10px; /* Add space between the logo and text */
}

.contact-logo {
  width: 24px; /* Adjust size as needed */
  height: 24px; /* Adjust size as needed */
}

.contact-link {
  text-decoration: none;
  color: white;
  display: flex;
  align-items: center;
  gap: 10px; /* Add space between the logo and text */
}

.contact-link:hover {
  color: #0000ff; /* Blue hover color */
  text-shadow: 0 0 32px #0000ff; /* Glow effect */
}

.contact-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px; /* Add space above the form */
  width: 100%; /* Ensure the form takes full width */
}

.contact-form label {
  margin: 10px 0;
  width: 100%; /* Full width for better alignment */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center align text and inputs */
}

.contact-form input,
.contact-form textarea {
  width: 100%; /* Full width input and textarea */
  max-width: 400px; /* Maximum width for consistency */
  padding: 10px;
  margin-top: 5px; /* Space between label and input */
  border-radius: 5px;
  border: 1px solid #ffffff;
  font-size: 1rem; /* Adjust font size for consistency */
  background-color: #000000;
  color: #ffffff;
  font-family: 'Kode Mono', monospace; /* Apply Google Font */
}

.contact-form button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: rgb(66, 0, 68);
  color: rgb(255, 255, 255);
  cursor: pointer;
  transition: background-color 0.3s ease, text-shadow 0.3s ease-in-out;
  font-size: 1rem; /* Adjust font size for consistency */
  margin-top: 10px; /* Space above button */
  font-family: 'Kode Mono', monospace; /* Apply Google Font */
}

.contact-form button:hover {
  background-color: #0000ff; /* Blue hover color */
  text-shadow: 0 0 32px #0000ff; /* Glow effect */
}

/* New styles for spacing around the span elements */
.contact-item span {
  margin: 10px 0; /* Add vertical space */
}
