/* File: src/components/NavBar.css */

/* Style umum */
nav {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #000000;
    z-index: 1000;
    box-shadow: 0 2px 50px rgba(24, 24, 24, 0.658);
}

ul {
    display: flex;
    justify-content: flex-start; /* Align items to the left */
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 75px;
    padding-left: 50px; /* Add space from the left */
}

li {
    margin: 0 20px;
}

a {
    text-decoration: none;
    color: white;
    font-weight: medium;
    font-size: 18px;
    transition: color 0.3s ease-in-out, text-shadow 0.3s ease-in-out;
    font-family: 'Kode Mono', monospace; /* Apply the Google Font */
}

a:hover {
    color: #882dff; /* Hover color */
    text-shadow: 0 0 32px #882dff98; /* Glow effect */
}

a.active {
    color: #6127ff; /* Active link color */
    text-shadow: 0 0 32px #6127ff9a; /* Glow effect */
}

.logo-container {
    margin-left: auto; /* Push logo to the right */
    padding-right: 50px; /* Add space from the right */
}

.logo {
    height: 50px; /* Adjust height as needed */
}

.navbar-logo {
    width: 100px; /* Sesuaikan ukuran logo */
    height: auto;
}



/* Media query untuk layar kecil (mobile) */
@media (max-width: 768px) {
    ul {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0;
        background-color: #000000;
        position: fixed;
        top: 75px;
        left: 0;
        width: 100%;
        height: 100vh;
        transform: translateX(-100%);
        transition: transform 0.3s ease-in-out;
    }

    ul.active {
        transform: translateX(0);
    }

    li {
        margin: 20px 0;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 20px;
        left: 50px; /* Pindahkan ke sisi kiri */
        cursor: pointer;
        font-size: 30px;
        color: white;
    }
}
