.footer {
    background-color: #000000;
    color: white;
    text-align: center;
    padding: 20px;
    position: relative;
    bottom: 0;
    width: 100%;
  }
  
  .footer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .social-icons {
    margin-top: 10px;
  }
  
  .social-icons a {
    color: white;
    margin: 0 10px;
    text-decoration: none;
  }
  
  .social-icons a:hover {
    color: #882dff; /* Hover color */
  }
  
  /* Menyembunyikan scrollbar tapi tetap bisa scroll */
  body {
    overflow-y: scroll; /* Enable vertical scroll */
  }
  
  body::-webkit-scrollbar {
    width: 0; /* Hide scrollbar */
  }
  
  body::-webkit-scrollbar-track {
    background: transparent; /* Background scrollbar track */
  }
  
  body::-webkit-scrollbar-thumb {
    background: transparent; /* Hide scrollbar thumb */
  }
  
  /* Custom scrollbar for mobile */
  @media only screen and (max-width: 768px) {
    body {
      overflow-y: auto; /* Enable vertical scroll */
    }
  
    body::-webkit-scrollbar {
      width: 8px; /* Width of the scrollbar */
    }
  
    body::-webkit-scrollbar-track {
      background: transparent; /* Background scrollbar track */
    }
  
    body::-webkit-scrollbar-thumb {
      background-color: #882dff; /* Scrollbar thumb color */
      border-radius: 10px; /* Rounded corners for the scrollbar */
      box-shadow: 0 0 10px #882dff; /* Glow effect */
    }
  
    body {
      overflow-x: hidden; /* Disable horizontal scrolling */
    }
  
    html, body {
      max-width: 100%; /* Prevent zooming */
    }
  }
  