/* File: src/components/Home.css */

#home {
    height: 100vh;
    display: flex;
    flex-direction: column; /* Ubah ke kolom untuk layar kecil */
    justify-content: center; /* Align items to center */
    align-items: center;
    position: relative;
    overflow: hidden;
    padding-top: 60px;
    color: white;
}

.model-container {
    width: 25%; /* Sesuaikan lebar model container */
    height: 50%; /* Sesuaikan tinggi model container */
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.overlay {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column; /* Ubah ke kolom untuk layar kecil */
    align-items: center;
    background-color: black;
    color: white;
    z-index: 1;
}
  
.text-container {
    text-align: center; /* Ubah teks ke tengah */
    font-family: 'Kode Mono', monospace;
    margin: 20px;
    max-width: 800px; /* Set a max width for better readability */
    padding: 0 80px; /* Add horizontal padding */

}
  
h1 {
    font-size: 24px; /* Sesuaikan ukuran font untuk layar kecil */
    font-weight: bold;
}
  
p {
    font-size: 16px; /* Sesuaikan ukuran font untuk layar kecil */
    font-weight: medium;
    line-height: 1.5; /* Add line height for better readability */
}

/* Media query untuk layar besar */
@media (min-width: 768px) {
    #home {
        flex-direction: row; /* Kembali ke baris untuk layar besar */
        justify-content: space-between; /* Align items to both sides */
        padding-top: 60px;
    }

    .model-container {
        width: 50%; /* Limit the width of the model container */
        height: 100%; /* Ensure it takes up the full height */
    }
  
    .overlay {
        flex-direction: row; /* Kembali ke baris untuk layar besar */
        align-items: center;
    }
  
    .text-container {
        text-align: left; /* Kembali ke kiri untuk layar besar */
        margin-left: 50px; /* Add space from the left */
        margin-right: auto;
    }
  
    h1 {
        font-size: 39px; /* Kembali ke ukuran font besar */
    }
  
    p {
        font-size: 17px; /* Kembali ke ukuran font besar */
    }
}
