/* File: src/components/Projects.css */

#projects {
  height: 100vh;
  background-color: rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
  color: white;
  font-family: 'Kode Mono', monospace;
}

.project-container {
  display: flex;
  align-items: center;
  text-align: center;
}

.project-details {
  margin: 20px;
}

.project-image {
  width: 100%;
  max-width: 700px;
  height: auto;
  margin: 20px 0;
}

.nav-buttons-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.nav-button {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 10px;
  transition: color 0.3s;
  margin: 0 175px; /* Jarak antara tombol */
}

.nav-button:hover {
  color: #ccc;
}
.demo-link {
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}

.demo-link:hover {
  color: #882dff; /* Hover color */
  text-shadow: 0 0 32px #882dff98; /* Glow effect */
}


/* Media query untuk layar kecil */
@media (max-width: 768px) {
  .project-container {
    flex-direction: column;
  }
}
