/* File: src/components/About.css */

#about {
    height: 150vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: visible; /* Change to visible */
    padding-top: 60px;
    color: white;
}


.model-container {
    width: 50%; /* Sesuaikan lebar model container */
    height: 25%; /* Sesuaikan tinggi model container */
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.overlay {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column; /* Ubah ke kolom untuk layar kecil */
    align-items: center;
    background-color: black;
    color: white;
    z-index: 1;
}
  
.text-about-container {
    text-align: center;
    font-family: 'Kode Mono', monospace;
    margin: 20px;
    max-width: 800px;
    padding: 0 80px; /* Add horizontal padding */
}
  
h1 {
    font-size: 21px; /* Sesuaikan ukuran font untuk layar kecil */
    font-weight: bold;
}
  
p {
    font-size: 10px; /* Sesuaikan ukuran font untuk layar kecil */
    font-weight: medium;
    line-height: 1.5; /* Add line height for better readability */
}

.photo-container {
    width: 100%; /* Full width for responsiveness */
    max-width: 250px; /* Maximum width for larger screens */
    height: 250px; /* Set height to make it a square */
    margin-top: 20px; /* Space between title and image */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; /* Hide overflow */
}

.photo-container img {
    width: 100%; /* Responsive width */
    height: 100%; /* Ensure height matches container */
    object-fit: cover; /* Crop to fill square */
    border-radius: 10px; /* Optional: rounded corners */
    box-shadow: 0 4px 8px rgba(255, 255, 255, 0.1); /* Optional: subtle shadow */
}





/* Media query untuk layar besar */
@media (min-width: 768px) {
    #about {
        flex-direction: row; /* Kembali ke baris untuk layar besar */
        justify-content: space-between; /* Align items to both sides */
        padding-top: 60px;
    }

    .model-container {
        width: 50%; /* Batasi lebar model container */
        height: 100%; /* Pastikan mengambil tinggi penuh */
    }
  
    .overlay {
        flex-direction: row; /* Kembali ke baris untuk layar besar */
        align-items: center;
    }
  
    .text-about-container {
        transform: translateY(-50px); /* Kembali ke transform untuk layar besar */
        text-align: left; /* Kembali ke kiri untuk layar besar */
        margin-left: 50px; /* Tambahkan ruang dari kiri */
        margin-right: auto;
    }
  
    h1 {
        font-size: 39px; /* Kembali ke ukuran font besar */
    }
  
    p {
        font-size: 17px; /* Kembali ke ukuran font besar */
    }
}
